import React, { Component } from 'react';
import Navigation from './components/Navbar'
import { Route, BrowserRouter } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './components/Home'
import Products from './components/Products'
import Contact from './components/Contact'
import CellTechnology from './components/CellTechnology'
import LPFVsNMC from './components/Cell'
import AboutChargeXO from './components/AboutChargeXO'
import Manufacturing from './components/Manufacturing'
import Applications from './components/Applications'
import TechCollab from './components/TechCollab'
import About from './components/AboutUS'


class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <Navigation />
          <Route exact path='/NewRevolutionXO' component={Home} />
          <Route path='/NewRevolutionXO/Products' component={Products} />
          <Route path='/NewRevolutionXO/contact' component={Contact} />
          <Route path='/NewRevolutionXO/CellTechnology' component={CellTechnology} />
          <Route path='/NewRevolutionXO/Cell' component={LPFVsNMC} />
          <Route path='/NewRevolutionXO/AboutChargeXO' component={AboutChargeXO} />
          <Route path='/NewRevolutionXO/Manufacturing' component={Manufacturing} />
          <Route path='/NewRevolutionXO/Applications' component={Applications} />
          <Route path='/NewRevolutionXO/TechCollab' component={TechCollab} />
          <Route path='/NewRevolutionXO/AboutUS' component={About} />

          

        </div>
        
      </BrowserRouter>
    );
  }
}

export default App;